import React, { useState, useEffect } from "react";
import "../Styles/slideshow.css";

import banner from "../Assets/banner.jpeg";
import junkdrawer from "../Assets/junkdrawer.jpeg";
import posters from "../Assets/posters.png";
import vandal from "../Assets/vandal.png";
import lib from "../Assets/lib.png";
import startup from "../Assets/startup.png";

function Slideshow() {
  const images = [banner, junkdrawer, posters, vandal, lib, startup];
  const [currentIndex, setCurrentIndex] = useState(0);
  const [fadeProp, setFadeProp] = useState({
    fade: 'fade-in'
  });

  useEffect(() => {
    const interval = setInterval(() => {
      setFadeProp({ fade: 'fade-out' });
      setTimeout(() => {
        setCurrentIndex((prevIndex) => (prevIndex + 1) % images.length);
        setFadeProp({ fade: 'fade-in' });
      }, 400); // Fade duration in ms
    }, 7000); // Slideshow interval (7 seconds)

    return () => clearInterval(interval);
  }, [images.length]);

  const handleThumbnailClick = (index) => {
    setFadeProp({ fade: 'fade-out' });
    setTimeout(() => {
      setCurrentIndex(index);
      setFadeProp({ fade: 'fade-in' });
    }, 500);
  };

  return (
    <div className="slideshow-container">
      {/* Removed arrow images and their handlers */}
      <img
        src={images[currentIndex]}
        alt="Slideshow"
        className={`slideshow-image ${fadeProp.fade}`}
      />

      <div className="thumbnails-container">
        {images.map((image, index) => (
          <img
            key={index}
            src={image}
            alt={`Thumbnail ${index + 1}`}
            className={`thumbnail ${index === currentIndex ? 'active' : ''}`}
            onClick={() => handleThumbnailClick(index)}
          />
        ))}
      </div>
    </div>
  );
}

export default Slideshow;
